import Uncheck from '@/static/img/icon-uncheck.png'
import Checked from '@/static/img/icon-checked.png'
import T from '@/utils/tips.js'
import {
	payAlipayByWap,
	payWXpayByWap
} from '@/api/payApi.js'
var vm = {
	name: 'pay',
	props: {
		show: {
			type: Boolean,
			default: false
		},
		isWx: {
			type: Boolean,
			default: true
		},
		price: {
			type: Number,
			default: 0
		},
		orderId: {
			type: String,
			default: ''
		},
		platform:{
			type: Number,
			default: 0
		}
	},
	data() {
		vm = this
		return {
			icon: {
				Uncheck,
				Checked
			},
			way: 0,
			resPayFrom: '',
			wxHref: '',
			checkIndex: 0,
			clock: true
		}
	},
	onload() {
		
	},
	onshow() {
		
	},
	methods: {
		wXpay() {

		},
		checkPayNav(index) {
			this.checkIndex = index
			this.clock = true
		},
		close() {
			vm.$emit('close', false)
		},
		h5WXpay(){
			if (this.orderId !== '') {
				let data = {
					payChannelEnum: 'WEIXIN_PAY',
					payWay:'WAP_PAY',
					orderId: this.orderId
				}
				payAlipayByWap(data).then(res => {
					if (res.code === '1000') {
						location.href = res.data.wxPayResp.payUrl;
						this.resPayFrom = ''
						this.clock = true
					} else {
						T.tips(res.message || '调用支付失败')
						this.clock = true
					}
				}).catch(err => {
					T.tips(err.message || '调用支付失败')
					this.clock = true
				})
			} else {
				T.tips('订单ID不存在')
				this.clock = true
			}
		},
		h5Alipay(){
			if (this.orderId !== '') {
				let data = {
					payChannelEnum: 'ALI_PAY',
					payWay:'WAP_PAY',
					orderId: this.orderId
				}
				payAlipayByWap(data).then(res => {
					if (res.code === '1000') {
						this.wxHref = '';
						this.resPayFrom = res.data.returnHtml
						this.clock = true
						setTimeout(() => {
							document.forms["punchout_form"].submit()
						}, 300)
					} else {
						T.tips(res.message || '调用支付失败')
						this.resPayFrom = ''
						this.clock = true
					}
				}).catch(err => {
					T.tips(err.message || '调用支付失败')
					this.resPayFrom = ''
					this.clock = true
				})
			} else {
				T.tips('订单ID不存在')
				this.clock = true
			}
		},
		// 小程序支付
		WXAppPay(){
			let _this = this
			if (this.orderId !== '') {
				let data = {
					payChannelEnum:'WEIXIN_PAY',
					payWay:'APPLET',
					orderId: this.orderId,
					openId: uni.getStorageSync('openid')
				}
				payAlipayByWap(data).then(res => {
					if (res.code === '1000') {
						console.log(JSON.stringify(res))
						let wxPayResp = res.data.wxPayResp;
						let obj = {
							appid: wxPayResp.appId,
							noncestr: wxPayResp.noncestr,
							package: 'Sign=WXPay', // 固定值，以微信支付文档为主
							partnerid: wxPayResp.partnerId,
							prepayid: wxPayResp.prepayId,
							timestamp: wxPayResp.timestamp,
							sign: wxPayResp.sign // 根据签名算法生成签名
						}
						orderInfo = JSON.stringify(obj);
						
						console.log('orderInfo',orderInfo)
						uni.requestPayment({
						    provider: 'wxpay',
						    orderInfo, //微信、支付宝订单数据
						    success: function (res) {
								_this.clock = true
						        console.log('success:' + JSON.stringify(res));
								uni.reLaunch({
									url:'/pages/user/pay/success?payPrice='+_this.price+'&orderId='+_this.orderId
								})
								
						    },
						    fail: function (err) {
								_this.clock = true
						        console.log('fail:' + JSON.stringify(err));
								T.tips('支付失败')
								uni.redirectTo({
								    url: '/pages/user/order/detail?orderId='+_this.orderId
								});
						    }
						});
					} else {
						T.tips(res.message || '调用支付失败')
						this.clock = true
					}
				}).catch(err => {
					T.tips(err.message || '调用支付失败')
					this.clock = true
				})
			} else {
				T.tips('订单ID不存在')
				this.clock = true
			}
		},
		// App支付
		AppPay(payChannelEnum){
			let _this = this
			if (this.orderId !== '') {
				let data = {
					payChannelEnum,
					payWay:'APP_PAY',
					orderId: this.orderId
				}
				payAlipayByWap(data).then(res => {
					if (res.code === '1000') {
						console.log(JSON.stringify(res))
						let wxPayResp = res.data.wxPayResp;
						let orderInfo = ''
						if(payChannelEnum == 'WEIXIN_PAY' ){
							let obj = {
								appid: wxPayResp.appId,
								noncestr: wxPayResp.noncestr,
								package: 'Sign=WXPay', // 固定值，以微信支付文档为主
								partnerid: wxPayResp.partnerId,
								prepayid: wxPayResp.prepayId,
								timestamp: wxPayResp.timestamp,
								sign: wxPayResp.sign // 根据签名算法生成签名
							}
							orderInfo = JSON.stringify(obj);
						}else{
							orderInfo = res.data.returnHtml
						}
						
						console.log('orderInfo',orderInfo)
						uni.requestPayment({
						    provider: payChannelEnum == 'WEIXIN_PAY' ? 'wxpay' : 'alipay',
						    orderInfo, //微信、支付宝订单数据
						    success: function (res) {
								_this.clock = true
						        console.log('success:' + JSON.stringify(res));
								uni.reLaunch({
									url:'/pages/user/pay/success?payPrice='+_this.price+'&orderId='+_this.orderId
								})
								// uni.showModal({
								// 	content:JSON.stringify(res),
								// 	success() {
								// 		uni.navigateTo({
								// 			url:'/pages/user/pay/success?payPrice='+_this.price+'&orderId='+_this.orderId
								// 		})
								// 	}
								// })
								
						    },
						    fail: function (err) {
								_this.clock = true
						        console.log('fail:' + JSON.stringify(err));
								T.tips('支付失败')
								uni.redirectTo({
								    url: '/pages/user/order/detail?orderId='+_this.orderId
								});
								// uni.showModal({
								// 	content:JSON.stringify(err),
								// 	success() {
										
								// 	}
								// })
								// uni.navigateTo({
								// 	url:'/pages/user/order/detail?orderId='+_this.orderId
								// })
						    }
						});
					} else {
						T.tips(res.message || '调用支付失败')
						this.clock = true
					}
				}).catch(err => {
					T.tips(err.message || '调用支付失败')
					this.clock = true
				})
			} else {
				T.tips('订单ID不存在')
				this.clock = true
			}
		},
		doPay() {
			if(!this.clock) return
			this.clock = false
			if (this.checkIndex === 0) { // 微信网页支付






				

				this.WXAppPay()

			} else { // 支付宝网页支付
				






			}
		}
	}
}
export default vm
